import {IsDistributor} from 'api/user'
import {UniTag} from 'components/uni/uni-tag/uni-tag'
import {UniBadge} from 'components/uni/uni-badge/uni-badge'
export default {
	components:{
		UniTag,
		UniBadge
	},
	data() {
		return {
			/*消息数*/
			msgNum:'0',
			userInfo:{},
			state:{},
			isLogin:0,
			userFace:'',
			baseUrl:this.$store.state.SRC+"/config/app",
			fenxiaoUrl:'',
			fenxiaoMsg:''
		}
	},
	onLoad() {	
		
	},
	onPullDownRefresh() {
		this.$store.dispatch('getUserInfo').then(()=>{
			uni.stopPullDownRefresh();
			this.initData()
		})
	},
	onShow() {
		IsDistributor().then(res=>{
			this.fenxiaoMsg = ''
			if(res.result){
				 if (res.code == 3){
					this.fenxiaoUrl = '/pages/user/distribution/index'
				}else if(res.code == 4){
					this.fenxiaoUrl = '/pages/user/distribution/apply'
				}
			}else{
				if(res.code == 2){
					this.fenxiaoMsg = res.msg
				}
			}
			this.initData()	
		})
		
	},
	methods: {
		initData(){
			this.$store.ready(state=>{
				this.state = state
				this.isLogin = state.isLogin
				this.userInfo = state.userInfo
				this.msgNum = state.userInfo.ReadTF || 0
				this.userFace = state.userInfo.UserFace
			})
		},
		login(url){
			uni.navigateTo({
				url:url
			})
		},
		open(url){
			console.log(url)
			if(this.isLogin == 1){
				uni.navigateTo({
					url:url
				})
			}else{
				uni.navigateTo({
					url:'/pages/user/login/index'
				})
			}	
		},
		openFenxiao(url){
			console.log(this.fenxiaoMsg)
			if(this.fenxiaoMsg){
				uni.showToast({
					title:this.fenxiaoMsg,
					icon:'none'
				})
			}else{
				if(this.isLogin == 1){
					uni.navigateTo({
						url:url
					})
				}else{
					uni.navigateTo({
						url:'/pages/user/login/index'
					})
				}
			}
			
		}
	}
}
